import { Component, OnInit } from '@angular/core';

@Component({
	moduleId: module.id,
  selector: 'fufeck-interest',
  templateUrl: './interest.component.html',
  styleUrls: ['./interest.component.css']
})
export class InterestComponent implements OnInit {

 text:string = `
<!-- Interets -->

<section>

<h1> Mes intérets </h1>

<br/>
<br/>

<p> Nombreux séjours à l’étranger, </p>
<p> Dix ans de Hand Ball en club, Badminton en compétition, </p>
<p> Cinq ans de conservatoire de guitare et batterie, </p>
<p> Conférences, Hackathons (Alstom, Streamnation). </p>

</section>
`;

  options:any = {
    maxLines: 1000,
    fontSize: "14px",
    highlightGutterLine: false,
    displayIndentGuides: true,
    showInvisibles: true,
    useWorker: false
  };
  constructor() { }

  ngOnInit() {
  }

}



