import { Component, OnInit } from '@angular/core';

@Component({
	moduleId: module.id,
  selector: 'fufeck-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {

 text:string = `
/* contact */

{
  "firstname" : "fabien",
  "lastname" : "tafforeau",
  "phone" : "07 83 96 31 84",
  "email" : "ftaffore@student.42.fr",
  "adress" : "30 rue du colombier, 91400 - Saclay"
}
`;

  options:any = {
    maxLines: 1000,
    fontSize: "14px",
    highlightGutterLine: false,
    displayIndentGuides: true,
    showInvisibles: true,
    useWorker: false
  };

  constructor() { }

  ngOnInit() {
  }

}



