import { Component, OnInit } from '@angular/core';

@Component({
	moduleId: module.id,
  selector: 'fufeck-skill',
  templateUrl: './skill.component.html',
  styleUrls: ['./skill.component.css']
})
export class SkillComponent implements OnInit {

  text:string = `
/* skills */

#environnement {
	macos : 80%;
	linux : 50%;
	windows : 50%;
}

#organisation {
	versionning : git;
	vm : Vagrant, Docker;
	methodologie : Agile, Scrum, Trello;
}

#programmation {
	base : C, C++;
	backend : NodeJs, Express, "Php", Zend, Symphony2;
	frontend : AngularJS, Meteor, "Html / Js / Css";
	mobile : Cordova, Ionic, "IOS / Swift", Java, Android;
}
`;

  options:any = {
  	maxLines: 1000,
  	fontSize: "14px",
  	highlightGutterLine: false,
  	displayIndentGuides: true,
  	showInvisibles: true,
    useWorker: false
  };

  constructor() { }

  ngOnInit() {}
}



