import { Component, OnInit } from '@angular/core';

@Component({
	moduleId: module.id,
  selector: 'fufeck-welcom',
  templateUrl: './welcom.component.html',
  styleUrls: ['./welcom.component.css']
})
export class WelcomComponent implements OnInit {

  text:string = `
<!-- welcom -->

<h1> A propos de moi </h1>
<br/>
<br/>
<p>
	Actuellement étudiant de 24ans en 3 ème année de l'école 42,
	je suis un jeune passionné, développeur de sites et d'applications web qui 
	habite à Saclay.
	Etant quelqu'un d'acharné avec le mental d'un sportif, je n'ai pas peur de 
	relever des défis.
	Grâce à Epitech et maintenant 42, j'ai pu développer une adaptation à toute 
	épreuve ainsi qu'une rapidité d'auto-formation rapide et efficace.
</p>
<br/>
<a href="https://github.com/fufeck"> github </a>
<br/>
<a href="https://www.linkedin.com/in/fabien-tafforeau-a98b47b3"> linkedin </a>
<br/>
`;

  options:any = {
    maxLines: 1000,
    fontSize: "14px",
    highlightGutterLine: false,
    displayIndentGuides: true,
    showInvisibles: true,
    useWorker: false
  };

  constructor() { }

  ngOnInit() {}

}



