import { Routes } from '@angular/router'

import { WelcomRoutes } from './welcom/index'
import { SkillRoutes } from './skill/index'
import { ContactRoutes } from './contact/index'
import { ExperienceRoutes } from './experience/index'
import { InterestRoutes } from './interest/index'


export const routes: Routes = [
	...WelcomRoutes,
	...SkillRoutes,
	...ContactRoutes,
	...ExperienceRoutes,
	...InterestRoutes,
	{ path: '',   redirectTo: '/welcom', pathMatch: 'full' },
  { path: '**', redirectTo: '/welcom' }
]


